"use client";

import { useSearchParams } from "next/navigation";
import HeartCursor from "@/components/HeartCursor";

export default function SummaryPage() {
  const searchParams = useSearchParams();
  const date = searchParams.get("date");
  const time = searchParams.get("time");
  const location = searchParams.get("location");
  const locationImage = searchParams.get("image");

  const formatDate = (dateStr: string) => {
    return new Date(dateStr).toLocaleDateString('en-US', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  const formatTime = (timeStr: string) => {
    return new Date(`2000/01/01 ${timeStr}`).toLocaleTimeString('en-US', {
      hour: 'numeric',
      minute: 'numeric',
      hour12: true
    });
  };

  return (
    
    <main className="min-h-screen flex items-center justify-center p-8 bg-[#fdf4f5]">
      <HeartCursor />
      <div className="max-w-2xl w-full">
      <h1 className="text-4xl font-bold text-center text-[#333] mb-12">
          SS ke akuu kaloo sudahhh yaaa ü§çü§ç
        </h1>
        <div className="bg-white rounded-2xl shadow-2xl overflow-hidden">
          {locationImage && (
            <div className="w-full h-64 relative">
              <img
                src={locationImage}
                alt="Date Location"
                className="w-full h-full object-cover"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-black/50 to-transparent" />
            </div>
          )}
          
          <div className="p-8 space-y-6">
            <div className="text-center space-y-2">
              <h1 className="text-3xl font-bold text-[#333]">
                Dwi Nurandina Rizal Basir
              </h1>
              <p className="text-xl text-[#f6a5c0] font-medium">
                Fakhreza&apos;s Girlfriend
              </p>
            </div>

            <div className="border-t border-b border-gray-100 py-6 space-y-4">
              <h2 className="text-2xl font-bold text-center text-[#333] mb-6">
                Our First Date
              </h2>
              
              <div className="space-y-3">
                <p className="flex justify-between text-lg">
                  <span className="font-medium text-[#666]">When:</span>
                  <span className="text-[#333]">
                    {date ? formatDate(date) : "Date not set"}
                  </span>
                </p>
                
                <p className="flex justify-between text-lg">
                  <span className="font-medium text-[#666]">Time:</span>
                  <span className="text-[#333]">
                    {time ? formatTime(time) : "Time not set"}
                  </span>
                </p>
                
                <p className="flex justify-between text-lg">
                  <span className="font-medium text-[#666]">Where:</span>
                  <span className="text-[#333]">
                    {location || "Location not set"}
                  </span>
                </p>
              </div>
            </div>

            <div className="text-center text-[#666] italic">
              "The beginning of our beautiful journey together"
            </div>
          </div>
        </div>
      </div>
    </main>
  );
}
